<template>
  <div class="login_container">
    <div class="header">
        <div>
          <router-link to="/Index"></router-link>
            <!-- <a href=""></a> -->
        </div>
        <div>会员登录</div>    
    </div>
    <div class="middle">
      <form action="">
        <div class="login">
         <div>
            <label for="">用户名:</label> 
            <div>
              <input type="text" v-model="phone" placeholder="手机号" class="myinput">
            </div>
          </div>
          <div>
              <label for="">密&nbsp;&nbsp;&nbsp;码:</label> 
            <div>
              <input  type="password" v-model="upwd" placeholder="密码" class="myinput">
            </div>
          </div>
          <div>
            <label for="">验证码:</label>
            <div class="confirm">
              <input type="text" placeholder="请输入验证码">
               <img src="../../assets/home/num.png" alt="">
            </div>
          </div>
          <div class="btn">
            <button @click="login">登 录</button><br>
          </div>
          <div class="last">
              <div>
                <input type="checkbox">&nbsp;&nbsp;自动登录
              </div>
               <span>
                   <a href="">免费注册</a>
                   <a href="">忘记密码?</a>
               </span>
          </div>
        </div>
      </form>
    </div>
  </div>
</template>
<script>
export default {
    data(){
        return{
            phone:"",
            upwd:""
        }
    },
    methods:{
        login(){
        //获取用户名和密码
          var ph=this.phone;
          var pw=this.upwd;
        //创建正则验证手机号
          var reg=/^1[3-9]\d{9}$/
        //创建正则验证密码：字母数字下划线3~12位
          var reg1=/^\w{3,12}$/i;
        //验证用户名，出错提示
        if(!reg.test(ph)){
          this.$toast("用户名格式不正确")
          return;
        };
        //验证密码，出错提示
         if(!reg1.test(pw)){
           this.$toast("密码格式不正确")
           return;
         };
         //5.发送ajax请求
         var url="login";
         var obj={phone:ph,upwd:pw};
         this.axios.get(url,{
           params:obj
         }).then(result=>{
           if(result.data.code>0){
             this.$router.push("../Home")
           }else{
             this.$messagebox("提示","用户名或密码错误")
           }
         });
        }
    }
}
</script>
<style scoped>
