     background: url("../../assets/home/arrow_left.png") no-repeat 15px center;
     background-size:auto 16px;
     display: block;
     width:45px;
     height:46px;
     margin:auto;
 }
  .header>div:last-child{
      float: left;
      width:70%;
      font-size: 16px;
      color:#fff;
      text-align: center;
      height:46px;
      line-height: 46px;
  }
 .middle{
    width:100%;
    margin:auto;
 }
 .login{
    width:90%;
    margin:auto; 
    padding-top:20px;
 }
 .login>div{
    width:100%;
    margin-top:15px;
    height:48px; 
 }
  .login>div>label{
    width: 25%;
