    border:1px solid #ddd;
    width:95%;
    padding-left: 5px;
    font-size: 14px;
 }
 input{
   outline: none;
 }
 .login>div>div>input:not(:last-child){
    height:35px;
    padding:0px;
    border:1px solid #ddd;
    width:95%;
    padding-left: 5px;
    font-size: 14px;
 }
 .login>div:nth-child(3)>div>input{
   width:60%;
   float:left;
 }
.login>div:nth-child(3)>div>img{
    width:30%;
    float:right;
    height:40px;
    vertical-align: middle;
}
.btn>button{
    width:100%;
